@keyframes bouncingText {
  0% {
    transform: scale(1, 1);
  }

  8% {
    transform: scale(1.1, 0.9);
  }

  16% {
    transform: scale(1.2, 0.8);
  }

  28% {
    transform: scale(1.5, 0.5);
  }

  44% {
    transform: scale(1.2, 0.8);
  }

  59% {
    transform: scale(0.8, 1.2);
  }

  73% {
    transform: scale(0.5, 1.5);
  }

  88% {
    transform: scale(0.8, 1.2);
  }

  94% {
    transform: scale(0.9, 1.1);
  }

  100% {
    transform: scale(1, 1);
  }
}

@keyframes scaleUpAndScaleDown1 {
  0% {
    transform: translate(-50%, -25rem) rotateZ(calc(-72deg * 0)) scale(1);
  }

  25% {
    transform: translate(-50%, -25rem) rotateZ(calc(-72deg * 0 - 90deg))
      scale(1.02);
  }

  50% {
    transform: translate(-50%, -25rem) rotateZ(calc(-72deg * 0 - 180deg))
      scale(1);
  }

  75% {
    transform: translate(-50%, -25rem) rotateZ(calc(-72deg * 0 - 270deg))
      scale(0.98);
  }

  100% {
    transform: translate(-50%, -25rem) rotateZ(calc(-72deg * 0 - 360deg))
      scale(1);
  }
}

